//styles
import styles from './MyPage.module.css';
import { Container } from 'react-bootstrap';
//MyPage - component
import MyBookTable from './MyBookTable';
import MyBookTableEmpty from './MyBookTableEmpty';
//hooks
import useUrl from '../../hooks/useUrl';
import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';

function MyPage() {
  const id = window.localStorage.getItem('userid');
  const navigate = useNavigate();
  const myUrl = useUrl();

  //----  userName  ----//
  const [bookingCount, setBookingCount] = useState([]);
  const [userName, setUserName] = useState('');
  const url = `http://${myUrl}/api/user/mypage?userId=${id}`;
  useEffect(() => {
    if (id === null) {
      alert('   .');
      navigate(`/`);
    } else {
      fetch(url, { method: 'GET' })
        .then((res) => res.json())
        .then((data) => {
          setUserName(data.userData.userName);
          setBookingCount(data.myBookingDetailDataList);
        });
    }
  }, [url, navigate, id]);
  //onsole.log(bookingCount);

  return (
    <Container style={{ padding: '0px 5px' }} className={styles.container}>
      <h6 className={styles.userInfo}>
         <span className={styles.user_name}>{userName}</span>
          
      </h6>
      {Array.isArray(bookingCount) && bookingCount.length === 0 ? (
        <MyBookTableEmpty />
      ) : (
        <MyBookTable />
      )}
    </Container>
  );
}

export default MyPage;
