//component
import React from 'react';
//styles
import styles from './RoomInfo.module.css';
//hooks
import { useParams } from 'react-router-dom';
import { useState, useEffect } from 'react';
import useUrl from '../../hooks/useUrl';
import useRoomImg from '../../hooks/useRoomImg';

function RoomInfo() {
  const { roomId } = useParams();
  const myUrl = useUrl();

  const [roomInfo, setRoomInfo] = useState('');
  const url = `http://${myUrl}/api/booking/room-data?floor=0&roomId=${roomId}`;
  useEffect(() => {
    fetch(url, {
      method: 'GET',
    })
      .then((res) => res.json())
      .then((data) => {
        setRoomInfo(
          data.roomData.filter((info) => info.roomId === Number(roomId))[0]
            .roomName
        );
      });
  }, [roomId, url]);

  //  room     roomId     
  const roomsImg = useRoomImg(); //useRoomImg  
  const [roomImg] = roomsImg.filter((img) => img.room_id === Number(roomId));

  return (
    <div className={styles.wrap}>
      <h3 className={styles.roomName}> {roomInfo} </h3>
      <img className={styles.room_img} alt='room_img' src={roomImg.img}></img>
      <div>
        <h6 className={styles.note}>    </h6>
        <div className={styles.contents}>
          <p>-     </p>
          <p>-     : 2-3 / 3-2</p>
          <p>-  1     </p>
          <p>- NaBox 1   </p>
          <p>-        OFF</p>
        </div>
      </div>
    </div>
  );
}
export default RoomInfo;
