import styles from './FourthFloorMap.module.css'

import { Link } from 'react-router-dom'

import { EmojiFrownFill } from 'react-bootstrap-icons'

const FourthFloorMap = ({ ablebtn, BookingConfirm, bookingData, roomData }) => {
  // roomFull  
  //        
  const Now = new Date()
  const NowHour = Now.getHours()
  const OverHour = NowHour + 1
  const RemainTime = 21 - OverHour

  // 09:00    9  
  const TimeToString = (time) => {
    let newTime
    if (time === '09:00') {
      newTime = time.substr(1, 1)
    } else {
      newTime = time.substr(0, 2)
    }
    return newTime
  }

  const notroomFull = (roomid) => {
    // room     
    const roomState = bookingData.filter(
      (room) =>
        room.roomId === roomid &&
        Number(TimeToString(room.startTime)) >= Number(OverHour)
    )

    //   
    const middleRoomState = bookingData.filter(
      (room) =>
        room.roomId === roomid &&
        Number(TimeToString(room.startTime)) < Number(OverHour) &&
        Number(TimeToString(room.endTime)) > Number(OverHour)
    )

    //       
    const roomBookingState = roomState.map(
      (room) =>
        TimeToString(room.endTime) - Number(TimeToString(room.startTime))
    )

    //        
    const middleBookingState = middleRoomState.map(
      (room) => Number(TimeToString(room.endTime)) - Number(OverHour)
    )

    //   
    const sum = roomBookingState.reduce(function add(sum, currValue) {
      return sum + currValue
    }, 0)

    //   
    const middleSum = middleBookingState.reduce(function add(sum, currValue) {
      return sum + currValue
    }, 0)

    //         
    return sum + middleSum < RemainTime

    //  
    // return [middleSum, sum, sum + middleSum < RemainTime]
  }

  return (
    <div className={styles.container}>
      <div className={styles.mapContainer}>
        {ablebtn ? null : (
          <h2 className={styles.alert}>
            <EmojiFrownFill />
            &nbsp;   
          </h2>
        )}
        {roomData.map((rooms) => (
          <Link
            to={`/booking/${rooms.roomId}`}
            key={rooms.roomId}
            className={styles[rooms.roomName]}
            id={
              notroomFull(rooms.roomId) && ablebtn
                ? [styles.MeetingRoom]
                : [styles.full]
            }
            onClick={BookingConfirm}
          >
            <div>
              {notroomFull(rooms.roomId) && ablebtn
                ? rooms.roomName
                : `${rooms.roomName}\n`}
            </div>
          </Link>
        ))}
        <div className={styles.office} id={styles.notSelect}>
          {`office`}
        </div>
      </div>
    </div>
  )
}

export default FourthFloorMap
